
body {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  font-family:'Roboto', sans-serif;
  background-color: #FFFAF0;
}
.Site {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: 'Roboto Condensed', sans-serif;
}
.Site-content {
  flex: 1 0 auto;
  width: 100%;
}
.Site-content:after {
  display: block;
  visibility: hidden;
}


  .home-wrapper{
    padding: 3em;
    margin: 0 auto;
    text-align: center;
  }

  .info-container {
    margin: 0 auto;
    width: 70%; 
  }

  .name{
        font-family: Arial, Helvetica, sans-serif;
        font-size: 4rem;
  }

  .title{
    font-family: Arial, Helvetica, sans-serif;
        font-size: 2rem;
  }

  .about{
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1.3rem;
    margin-top: 2rem;
    margin-bottom: 2rem;
  }
 
  .social-container{
    display: flex;
    flex-flow: row wrap;
    justify-content: space-evenly;
    align-items: center;
  }

  a{
     color: black;
     text-decoration: none;
  }

   a:hover{
    color: #1875D1;
    cursor: pointer;
  }


  @media all and (max-width: 430px){
    .social-container{
        flex-flow: column wrap;
    }
    .social-container a{
        padding-top: 1em;
    }

}



 

   
  
  
.list-item {
    position: relative;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    width: 45%;
    height: auto;
    margin: 1em auto;
    padding: 0;
    text-align: center; 
    border-radius: 10px;
    box-shadow: 2px 2px 10px 0 rgba(0,0,0,.3);
}
 
.list-item:hover .list-content {
    opacity: 0.80; 
}


.list-image {
  object-fit: fill;
  width: 100%;
  height: auto;
  overflow: hidden;
  border-radius: 10px;
}

      
.list-content {
    background-color: black;
    position: absolute;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    padding: 1rem;
    color: #00BFFF;
    font-size: 15px;
    opacity: 0;
    border-radius: 10px;
    -webkit-transition: opacity 0.25s ease-in 0.1s;
    transition: opacity 0.25s ease-in 0.1s; 
}


.list-links-container{
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-around;
    width: 30%;
}

.list-links-container a {
    color: #00BFFF;
    font-size: 1.2rem;
}

.list-links-container a:hover {
    color: #FAF0E6;
    text-decoration: underline;
}

  
@media all and (max-width: 900px){
    .list-item{
        width: 80%; 
        flex-grow: 1;
        height: auto;
        flex-basis: auto;
    }
    
}


@media all and (max-width: 600px){
    .list-item{
        width: 100%;
    }

}


.projects-container {
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-around;
    align-items: center;
    width: 81vw;
    padding: 1em;
    margin: 0 auto;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.projects-list{
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: center;
    width: 90%;  
    margin: 0 auto;
    padding: 2em;
    list-style: none; 
    flex: 1 1; 
}


@media all and (max-width: 900px)  {
    .projects-list {
        flex-flow: column wrap;
        justify-content: center;
        align-content: center;
        align-items: center;
    }
}

@media all and (max-width: 600px)  {
    .projects-list {
        width: 100vw;
    }

}

.footer-container {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    height: 100px;
  }
  
  .footer-links{
    display: flex;
    justify-content: space-around;
    width: 25rem;
    font-size: 1.5rem;
  }
  
  i{
     color: black;
  }
  
   i:hover{
    color: #1875D1;
    cursor: pointer;
  }
  
  .copy {
    margin-bottom: 5rem;
    text-align: center
  }
  
